.App {
    min-height: 100vh;
    background-color: #282c34;
    display: flex;
    flex-direction: column;
    font-size: calc(10px + 1vmin);
    color: white;
}

.form {
    padding: 32px;
    display: flex;
    flex-direction: column;
}

.inputContainer {
    display: flex;
    flex-direction: row;
    margin: 8px 0;
}


.inputContainer label {
    width: 300px;
}

.inputContainer input {
    width: 300px;
}

.inputContainer input[type=text] {
    width: 150px;
}

.form button {
    width: 200px;
    margin: 20px 20px 20px 0;
}

.form .buttonContainer {
    margin-top: 32px;
    display: flex;
}

.outputs {
    display: flex;
}

.outputs button {
    width: 200px;
    margin: 20px
}
